import React, { Component } from 'react';
import {
    withRouter
} from 'react-router-dom'
import './ShopRecharge.scss';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { is, fromJS } from 'immutable';
import { Rate,Modal } from 'antd';

class ShopRecharge extends Component{
    static propTypes = {
        qrcode: PropTypes.string.isRequired,
        isShow: PropTypes.bool.isRequired,
        onCancel: PropTypes.func.isRequired,
        data: PropTypes.object.isRequired
    };
    shouldComponentUpdate(nextProps, nextState) {
        return !is(fromJS(this.props), fromJS(nextProps))|| !is(fromJS(this.state),fromJS(nextState))
    };
    componentDidMount(){
    };
    render(){
        const { qrcode,onCancel,isShow,data } = this.props;
        return(
            <div className="record-user_recharge">
                <Modal
                    visible={isShow}
                    wrapClassName="vertical-center-modal"
                    title="分号充值"
                    width={500}
                    destroyOnClose={true}
                    onCancel={onCancel}
                    footer={null}
                >
                    <div className="order_detail">
                        <div className="top">
                            <div className="qrcode">
                                <img src={qrcode} alt=""/>
                            </div>
                            <div className="time">
                                <span>时间:</span>
                                <span>2018-03-04 14:14:14</span>
                            </div>
                            <div className="account">
                                <span>分号:</span>
                                <span>zhangsan</span>
                            </div>
                            <div className="nick_name">
                                <span>微信昵称:</span>
                                <span>张三</span>
                            </div>
                            <div className="pay_account">
                                <span>微信账户:</span>
                                <span>dsds</span>
                            </div>
                            <div className="order_no-evluation">
                                <div className="order_no">
                                    <span>订单号:</span>
                                    <span>123456789</span>
                                </div>
                                <div className="evluation">
                                    <span>评价:</span>
                                    <span>
                                    <Rate allowHalf disabled defaultValue={2.5} />
                                </span>
                                </div>
                            </div>
                        </div>
                        <div className="bottom">
                            <div>
                                <div className="amount">
                                    <span>交易量:</span>
                                    <span>1234.56</span>
                                    <small>KGT</small>
                                </div>
                                <div className="pay_type">
                                    <span>方式:</span>
                                    <span>微信</span>
                                </div>
                            </div>
                            <div>
                                <div className="price">
                                    <span>KGT价格:</span>
                                    <span>1.10</span>
                                    <small>CNY/KGT</small>
                                </div>
                                <div className="operation">
                                    <span>操作方式:</span>
                                    <span>充值</span>
                                </div>
                            </div>
                            <div>
                                <div className="num">
                                    <span>交易额:</span>
                                    <span>1234.56</span>
                                    <small>CNY</small>
                                </div>
                                <div className="status">
                                    <span>状态:</span>
                                    <span>完成</span>
                                </div>
                            </div>
                        </div>
                        <div className="btn_wrap">
                            <div className="channel">取消订单</div>
                            <div className="sure">确认收款</div>
                        </div>
                    </div>
                </Modal>
            </div>
        );
    }
}
const mapStateToProps = (state) => ({
    shopList: state.shopList,
    coinDetail: state.marketCoin.coinDetail,
    loginData: state.loginData
});

export default withRouter(connect(
    mapStateToProps
)(ShopRecharge))


